<script>
import store from '../../store/store.js'
import SearchBar from "../../src/components/Searchbar.vue"



export default {
  name: 'Navbar',
  components: { 
    SearchBar,
  },

  props: {
    msg: String
  },
  methods: {
    logout() {
      window.location.href = process.env.VUE_APP_ROOT_API + "accounts/logout"
    },
    login() {
      window.location.href = process.env.VUE_APP_ROOT_API + "accounts/login/"
    },
    goToProfile() {
      if (!store.state.username)  this.login() //this.$router.push('/accounts/login')
      else this.$router.push('/profile/')
    },
     searching() {
      
      let searchInput = document.getElementById("search-autocomplete")
      
      if (window.innerWidth < 992 ) {
        store.state.searching = !store.state.searching
        if(store.state.searching === true) {
          
          
          searchInput.style.width = "500px"
        } else {
          searchInput.classList.add("d-none")
          searchInput.style.width = "240px"
        }
        
      }
     },
  },
 mounted() {
    store.state.modalConfirm = true
    fetch(process.env.VUE_APP_ROOT_API + "api/user/")
      .then((res) => res.json())
      .then((data) => (store.state.username = data.username))
      .catch((err) => console.log(err.message));
  },

    
}
</script>

